import {RecommendWrapper} from "./style";

export default function RecommendList({recommend}) {
  return (
    <RecommendWrapper>
      <h2></h2>
      <div className="list">
        {
          recommend.map(item => (
            <div key={item.id}>
              <img src={item.img} alt=""/>
              <p className="title">{item.title}</p>
              <button className="coupon">{item.coupon}</button>
              <p className='price'><span style={{fontSize:'16px',fontWeight:'normal'}}> </span>{item.price}</p>
            </div>
          ))
        }
      </div>
    </RecommendWrapper>
  )
}
